"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";
import api from "@/lib/api";
import Form from "@/components/form";

export default function NovoProduto() {
  const [produto, setProduto] = useState({
    codigo: "",
    descricao: "",
    codigoBarras: "",
    valorVenda: "",
    pesoBruto: "",
    pesoLiquido: "",
  });

  const router = useRouter();

  const handleChange = (e) => {
    setProduto({ ...produto, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem("token");
      if (!token) {
        alert("Usuário não autenticado");
        return;
      }

      await api.post(
        "/produtos",
        {
          ...produto,
          usuarioNome: "-", // Campo obrigatório
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      router.push("/produtos");
    } catch (error) {
      console.error("Erro ao criar produto:", error.response?.data || error);
      alert("Erro ao criar produto. Veja o console.");
    }
  };

  const fields = [
    { name: "codigo", label: "Código", required: true, type: "number" },
    { name: "descricao", label: "Descrição", required: true },
    { name: "codigoBarras", label: "Código de Barras", required: true },
    { name: "valorVenda", label: "Valor de Venda", required: true, type: "number" },
    { name: "pesoBruto", label: "Peso Bruto (kg)", type: "number" },
    { name: "pesoLiquido", label: "Peso Líquido (kg)", type: "number" },
  ];

  return <Form title="🆕 Criar Produto" fields={fields} values={produto} onChange={handleChange} onSubmit={handleSubmit} />;
}
